.results__container { padding: 200px 0 158px; margin: 0 auto; max-width: 1007px; }

.results__container:after { content: " "; display: table; clear: both; }

@media only screen and (max-width: 1023px) { .results__container { padding: 30px 0 60px; } }

.results__title { padding: 54px 0 6px; color: #000; background: #fff; font-family: "Roboto-Bold", Helvetica Neue, Helvetica, Arial, sans-serif; font-weight: normal; font-size: 16px; line-height: 3.75; letter-spacing: 0; text-align: center; text-transform: uppercase; }

@media only screen and (max-width: 1023px) { .results__title { padding-top: 13px; } }

.results__info { float: left; padding-top: 6px; max-width: 200px; }

@media only screen and (max-width: 1023px) { .results__info { float: none; padding-bottom: 20px; margin: 0 auto; } }

.results__box { padding: 28px 0 26px; }

@media only screen and (max-width: 1023px) { .results__box { padding: 20px 0 0; } }

.results__box:not(:last-child):after { content: ''; display: block; border-bottom: 1px solid #d1d1d1; max-width: 190px; }

.results__subtitle { margin-bottom: 6px; font-family: "OpenSans-Bold", Helvetica Neue, Helvetica, Arial, sans-serif; font-weight: normal; font-size: 24px; line-height: 2.5; text-transform: uppercase; }

.results__content { font-family: "Roboto-Thin", Helvetica Neue, Helvetica, Arial, sans-serif; font-weight: normal; font-size: 14px; line-height: 2; letter-spacing: 0.04em; margin-bottom: 68px; }

@media only screen and (max-width: 1023px) { .results__content { margin-bottom: 30px; } }

.results__images { position: relative; float: right; width: 70%; max-width: 617px; height: 476px; }

@media only screen and (max-width: 1023px) { .results__images { float: none; margin: 0 auto; width: 100%; } }

@media only screen and (max-width: 767px) { .results__images { max-width: 320px; min-height: 550px; } }

.results__image { position: absolute; width: 320px; max-width: 100%; }

@media only screen and (max-width: 767px) { .results__image { position: relative; } }

.results__image + .results__image { margin-top: 30px; }

.results__image[data-item="1"] { z-index: 10; top: -3px; left: 0; }

@media only screen and (max-width: 767px) { .results__image[data-item="1"] { top: auto; left: auto; } }

.results__image[data-item="2"] { z-index: 5; right: 0; bottom: 0; }

@media only screen and (max-width: 767px) { .results__image[data-item="2"] { right: auto; bottom: auto; } }

.results__image-in { width: 100%; height: 0; padding-bottom: 100%; background-repeat: no-repeat; background-position: center center; background-size: cover; background-color: #f8f8f8; }

.contact { padding: 260px 0 187px; color: #fff; background: #232323; }

@media only screen and (max-width: 1023px) { .contact { padding: 80px 0; } }

.contact__container { margin: 0 auto; max-width: 990px; }

.contact__container:after { content: " "; display: table; clear: both; }

.contact__info, .contact__form { float: left; padding: 0 15px; }

@media only screen and (max-width: 1023px) { .contact__info, .contact__form { float: none; padding: 0; margin: 0 auto; width: 100%; max-width: 480px; } }

.contact__info { width: 49.5%; }

.contact__info-in { max-width: 400px; }

.contact__info p { font-family: "Roboto-Thin", Helvetica Neue, Helvetica, Arial, sans-serif; font-weight: normal; font-size: 16px; line-height: 1.75; letter-spacing: 0.048em; }

.contact__form { width: 50.5%; }

.contact__title { margin-bottom: 15px; opacity: 0.35; font-family: "OpenSans-Extrabold", Helvetica Neue, Helvetica, Arial, sans-serif; font-weight: normal; font-size: 80px; line-height: 1.125; transition: opacity 0.4s ease-in-out; }

@media only screen and (max-width: 767px) { .contact__title { max-width: 300px; font-size: 48px; } }

.contact__title.is-active { opacity: 0.65; }

.contact__field { padding: 21px 0; }

.contact__buttons { padding-top: 15px; }

.contact__btn { color: #fff; opacity: 0.5; background: none; font-family: "OpenSans-Extrabold", Helvetica Neue, Helvetica, Arial, sans-serif; font-weight: normal; font-size: 12px; line-height: 1.66667; text-transform: uppercase; transition: opacity 0.175s ease-in-out; }

.no-touch .contact__btn:hover { opacity: 1; }

.footer { position: relative; padding: 99px 0; }

@media only screen and (max-width: 767px) { .footer { padding: 40px 0; background: #fff; } }

.footer:before, .footer:after { content: ''; position: absolute; top: 0; bottom: 0; width: 50%; }

@media only screen and (max-width: 767px) { .footer:before, .footer:after { display: none; } }

.footer:before { left: 0; background: #fff; }

.footer:after { right: 0; background: #f5f5f5; }

.footer__row { margin: 0 -15px; }

.footer__row:after { content: " "; display: table; clear: both; }

.footer__col { float: left; padding: 0 15px; width: 50%; }

@media only screen and (max-width: 767px) { .footer__col { padding: 20px 15px; width: 100%; } }

.footer__nav { margin: 0 -25px 90px 0; }

.footer__nav:after { content: " "; display: table; clear: both; }

@media only screen and (max-width: 767px) { .footer__nav { margin-bottom: 50px; } }

.footer__list, .footer__logo { float: left; width: 50%; }

.footer__logo { padding: 4px 12px 0; }

.footer__logo a { display: inline-block; vertical-align: top; width: 66px; color: #000; font-family: "Roboto-Thin", Helvetica Neue, Helvetica, Arial, sans-serif; font-weight: normal; font-size: 12px; line-height: 2.66667; letter-spacing: 0.04em; }

.footer__logo img { display: inline-block; vertical-align: top; width: 100%; }

.footer__link { position: relative; display: inline-block; vertical-align: top; color: #000; font-family: "Roboto-Thin", Helvetica Neue, Helvetica, Arial, sans-serif; font-weight: normal; font-size: 12px; line-height: 2.66667; letter-spacing: 0.04em; transition: opacity 0.15s ease-in-out; }

.no-touch .footer__link:hover { opacity: 0.6; }

.footer__description { padding-left: 90px; margin: 0 auto; max-width: 320px; color: #343434; font-family: "Roboto-Thin", Helvetica Neue, Helvetica, Arial, sans-serif; font-weight: normal; font-size: 14px; line-height: 2.28571; letter-spacing: 0.04em; text-align: center; }

@media only screen and (max-width: 767px) { .footer__description { display: none; } }

.footer__description p { margin-bottom: 33px; }

.footer__social { padding: 3px 0 0 93px; color: #000; font-size: 0; text-align: center; }

@media only screen and (max-width: 767px) { .footer__social { padding: 0; } }

.footer__social li, .footer__social a { display: inline-block; vertical-align: top; }

.footer__social a { padding: 7px 8px; color: #000; transition: opacity 0.15s ease-in-out; }

.no-touch .footer__social a:hover { opacity: 0.6; }

.footer__social .icon { fill: currentColor; font-size: 32px; }

[data-show-img*="parent"] { overflow: hidden; transform: translate3d(-100%, 0, 0); }

[data-show-img*="parent"].is-active { transform: translate3d(0, 0, 0); transition-delay: 0.3s; }

[data-show-img*="parent"].is-active [data-show-img*="children"] { transition-delay: 0.3s; transform: translate3d(0, 0, 0); }

[data-show-img*="parent"].is-active [data-show-img*="opacity"] { opacity: 1; }

[data-show-img*="children"] { transform: translate3d(100%, 0, 0); }

[data-show-img*="opacity"] { opacity: 0; }

[data-show-img] { transition: transform 1s cubic-bezier(0.8, 0, 0.2, 1), opacity 1.1s ease 167ms; }

[data-stagger*="from-"] { position: relative; opacity: 0; display: block; }

[data-stagger="from-top"] { transform: translateY(-30px); }

[data-hidden-anim-parent] { overflow: hidden; }

[data-show-onscroll].is-active [data-hidden-anim^="from-"] { transform: translateY(0); transition-delay: 0.3s; }

[data-show-onscroll].is-active .line[data-hidden-anim^="from-"] { transition-delay: 0s; }

[data-hidden-anim^="from-"] { position: relative; transition: transform .75s ease-out; display: block; }

[data-hidden-anim="from-top"] { transform: translateY(-100%); }

[data-hidden-anim="from-bottom"] { transform: translateY(100%); }

[data-hidden-anim="from-left"] { transform: translateX(-100%); }

[data-hidden-anim="from-right"] { transform: translateX(100%); }
